import React from 'react';
import { useRef, useState, useEffect } from 'react';
import Webcam from 'react-webcam';
/*
import  { parseISO} from 'date-fns';
*/
import { useNavigate, useLocation } from 'react-router-dom';
import { useIsOnline } from 'react-use-is-online';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import { cirurgicalFlow } from '../../../../menu';
import Input from '../../../../components/bootstrap/forms/Input';
import Button, { ButtonGroup } from '../../../../components/bootstrap/Button';
import Modal, {ModalBody,ModalFooter,ModalHeader,ModalTitle} from '../../../../components/bootstrap/Modal';
import Header from '../../../../components/Header';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Card, {CardBody} from '../../../../components/bootstrap/Card';
import Icon from '../../../../components/icon/Icon';
import API, {api} from '../../../../Services';

import NumericKeyboard from '../../../../components/Teclado';
// eslint-disable-next-line no-unused-vars
import style from './style.module.css'





const AssinaturaCheckList = () => {
	


const { isOnline } = useIsOnline()
const navigate = useNavigate()
const location = useLocation()
const webRef = useRef(null);
const inputRef = useRef(null);	
const [DadosPaciente, 		setDadosPaciente	]		= useState([])
const [data, 				setData] 					= useState('')
const [confirmationModal, 	setConfirmationModal] 		= useState(false)
const [modalSuccess, 		setModalSuccess] 			= useState(false)
const [dadosRedirect, 		setDadosRedirect] 			= useState({})
const [Loading, 			setLoading] 				= useState(false)
const [modalPermissaoSuspensao, 		setModalPermissaoSuspensao] 			= useState(false)


const [msgErrorAss,  setMsgErrorAss] 			= useState('')
const [modalErrorMv, setModalErrorMv] 			= useState(false)
const [nofaceS3Error, 		setNofaceS3Error] 			= useState(false)




let userCheck;	

useEffect(() => {
	setDadosRedirect({...location.state})
	console.log(location.state)
	
	if(location.state){
		setDadosPaciente(location.state)
	 }else{
		 navigate('/')
	}
}, [location, data, navigate]);


/*
* Caso seja clicado no MENU na barra superior, irá quebrar o LocalStorage e retornará a página inicial.
*/


const alterarLocalidade = () => {
	localStorage.removeItem('CAPP-local');
	navigate('/')
};


const childToParent = (childData) => {
	const valor = childData;
		if(valor === 'apagar'){
			const valorMenos = inputRef.current.value.substring(0, inputRef.current.value.length - 1)
			inputRef.current.value = valorMenos;
			
		}
		else if(valor === 'zerar'){
			inputRef.current.value = '';
			
		}else{
			
			setData(valor);
			inputRef.current.value += valor;
			
		}
}	


const videoConstraints = {
	width: 540,
	height: 880,
	facingMode: "user"
};




const getImage = () => {
	return webRef.current.getScreenshot();
};

	
const handleError = async (image,badge,user,aviso,etapa,fileName,autenticado, tipoError) =>{
	setLoading(false)
	console.log(`Entrou no handleError: ${JSON.stringify(image)}`)
	
	// eslint-disable-next-line default-case
	switch(tipoError){
		case 0:
			setLoading(false)
			setNofaceS3Error(true);
		break;
		case 1:
			setConfirmationModal(true)
			await imageSave(image,	badge,	user,	aviso,	etapa,	fileName,	autenticado)
		break;
	}
	
}

	const handleSuccess = async(image,badge,user,aviso,etapa,fileName,autenticado) =>{
		setLoading(false)
		setModalSuccess(true)
		await imageSave(image,badge,user,aviso,etapa,fileName,autenticado)
	}



/**
	 * 
	 * Função para salvar os dados de histórico de assinatura no back-end.
	 */

 const imageSave = async (image,badge,user,aviso,etapa,fileName,autenticado) =>{

	console.log("Entrou no imageSave")

	const params = {
		image: `${image}`,			
		badge: `${badge}`,
		filename:`${fileName}`,
		user: `${user}`,
		aviso: `${aviso}`,
		etapa: `${etapa}`,
		autenticado: `${autenticado}`,
	}

	const resSendImage = await sendFile(params);
		
	console.log(JSON.stringify(resSendImage));

	try{			

		
		await API.post('/salvarHistoricoAutenticacao', params);
		 
		

	}catch(error){

		console.log(`error imageSave: ${error}`)
	}
}

const sendFile = async (params) => {
		 
	const res = await API.post(`/uploadImage`,params)
	console.log(res);
}

const redirecionarPagina = async()=>{
	navigate('/');
}
	const isUser = async (img,badg) =>{
		setLoading(true)
		/**
		 * verifica se tem internet, 
		 */

		if(!isOnline)
		{
			alert('Você está sem acesso à internet!')
			return
		}


		const params = {
			imageSource: `${img}`,
			internCode: 1,
			keyName: `${badg}`,
			option: 'compare',
			PAPEL_ACAO: 156			
		}

	
	
		try{
			/**
			 * Irá enviar a imagem pro reconhecimento e verificará se o mesmo possui internet,
			 * Caso não houver, buscará o nome do usuário através do código informado, para realizar o salvamento da imagem e no LOG do banco quem realizou a assinatura.
			 */
			 const identificaCodUsuario = await API.get(`/retornaCodUsuario?codchapa=${inputRef.current.value}`);
			 if(!identificaCodUsuario.data[0])
			 {
				// Não conseguiu identificar o usuário, checar qual erro está retornando.
				 
		 
				const retornarMotError = await API.get(`/erroUserCracha/${badg}`);
				const responseMotError = retornarMotError.data;
				
 
				setLoading(false)
				setMsgErrorAss(`${responseMotError}`)
				setModalErrorMv(true)
 
 
			   // setConfirmationModal(true)
				return
			 }

			const COD_USUARIO = identificaCodUsuario.data[0].CD_USUARIO
			

			userCheck = await api.post('/rekognition/face',  params);
			console.log(userCheck.data)

			console.log('dadosRedirect',dadosRedirect);
			
			console.log(COD_USUARIO);
		
			const localSession = JSON.parse(localStorage.getItem('CAPP-local'))

			/*
			if(userCheck.data.internet)
			{
				// eslint-disable-next-line default-case
				switch(location.state.TP_ACAO)
				{
						case 'ALTERAR_DATA_JEJUM':
							await handleSuccess(img,badg,userCheck.data[0].user,location.state.DADOS.DADOS.COD_AVISO,null,`${badg}_${userCheck.data[0].user}_${null}_${null}`,'S');
							await insereRepostaChecklist(userCheck.data[0].user, location.state.CHECKLIST)
							await submitData(userCheck.data[0].user)
						break;
						case 'CANCELAR_CIRURGIA':
							await imageSave(img,badg,userCheck.data[0].user,location.state.DADOS.DADOS.COD_AVISO,null, `${badg}_${userCheck.data[0].user}_${null}_${null}`, 'S')	
							await suspenderCirurgia(location.state.DADOS.DADOS.COD_AVISO, userCheck.data[0].user, location.state.MOTIVO, localSession.CODIGO_SUBLOCALIDADE);
						break;
				}
			}

			*/

			if((userCheck.data[0].resposta === false) || (userCheck.data[0].user !== COD_USUARIO))
			{
				await handleError(img,badg,userCheck.data[0].user,location.state.DADOS.DADOS.COD_AVISO,null,`${badg}_${userCheck.data[0].user}_${null}_${null}`,'N', userCheck.data[0].codigo);
				console.log('checklist 2',location.state.CHECKLIST);
				await insereRepostaChecklist(userCheck.data[0].user, location.state.CHECKLIST)
			    await submitData(userCheck.data[0].user)
				return;
			}

				
				// eslint-disable-next-line default-case
				switch(location.state.TP_ACAO)
				{
						case 'ALTERAR_DATA_JEJUM':
							await handleSuccess(img,badg,userCheck.data[0].user,location.state.DADOS.DADOS.COD_AVISO,null,`${badg}_${userCheck.data[0].user}_${null}_${null}`,'S');
							await insereRepostaChecklist(userCheck.data[0].user, location.state.CHECKLIST)
							await submitData(userCheck.data[0].user)
						break;
						case 'CANCELAR_CIRURGIA':
							await imageSave(img,badg,userCheck.data[0].user,location.state.DADOS.DADOS.COD_AVISO,null, `${badg}_${userCheck.data[0].user}_${null}_${null}`, 'S')	
							await suspenderCirurgia(location.state.DADOS.DADOS.COD_AVISO, userCheck.data[0].user, location.state.MOTIVO, localSession.CODIGO_SUBLOCALIDADE);
						break;
				}

			
			

		}catch(error){
			console.log(error)
		}

	
	} 

	const closeModal = () => {
		setConfirmationModal(false);
	};

	const insereRepostaChecklist = async (paramNome, objeto) =>{
		
		try {
			const cadastra = await API.post(`/insertRepostasChecklist`, { questionsCheckList: objeto, Usuario: paramNome });
			const response = await cadastra.data[0]
						
			if (response) {
				console.log('Sucesso!')
			}
		} catch (error) {
			console.log(error);
		}
	
	}


	const suspenderCirurgia = async(cdAviso, cdUsuario, cdAvisoCancelamento, cdSubLocalidade) => {
		const suspendeCirurgia = await API.post('/suspenderCirurgia',  {
				CD_AVISO: 					cdAviso,
			 	CD_USUARIO: 				cdUsuario,
			 	CD_AVISO_CANCELAMENTO: 		cdAvisoCancelamento,
			 	CD_SUB_LOCALIDADE: 			cdSubLocalidade
		});
		const responseSuspendeCirurgia = suspendeCirurgia.data;
		console.log(responseSuspendeCirurgia);
	
		if(!responseSuspendeCirurgia)
		{
			setLoading(false)
			setModalPermissaoSuspensao(true)
			setTimeout(
				()=> navigate('/')
			, 3000)
		}else{
			setLoading(false)
			setModalSuccess(true)
			setTimeout(
				()=> navigate('/')
			, 3000)

		}
		
	}


	const submitData = async (paramNome) =>{
		console.log(paramNome)
		setTimeout(
			await redirecionarPagina()
		, 80000)
		

};


const localidadeHeader = `Sala de Preparo`;

return (
		<PageWrapper title={cirurgicalFlow.PREPARO_ASSINATURA_ALTERACOES.text}>
				<Header titulo={localidadeHeader} localidade='Assinatura Colaborador'>
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>
				{/** INICIO */}
<div className={style.container}>
<header>
    <div>
        <Card className={style.area_header_info_patient} >
                <CardBody>
                    <h3>Paciente</h3>
                    <h1>{DadosPaciente.DADOS ? DadosPaciente.DADOS.DADOS.PACIENTE : '- - '}</h1>
                </CardBody>
        </Card>
    </div>
</header>
<main className={style.grandArea}>
    {/* área onde haverá a câmera */}
    <div className={style._area_photo_and_chapa}>
        <div className={style.webcam_area}>
            <Webcam ref={webRef} className={style.cam} audio={false}  screenshotFormat="image/jpeg" videoConstraints={videoConstraints} />
        </div>
    </div>
    {/* área onde haverá o input do crachá + teclado */}
    <div className={style._area_teclado}>
        <div style={{"padding": "1rem"}}>
                    <div style={{width: "400px"}}>
                        <h2>Informe o código do crachá</h2>
                        <Input className={style.input_chapa} type="number" disabled  ref={inputRef}/>	
                        <br />
                    </div>
                        <div style={{width: "400px"}}>
                            <NumericKeyboard childToParent={childToParent} /> 
                        </div>
                        {/* área onde haverá o input de assinar + botão de retornar */}
                        <div className={style._area_inputs}>
                            <div>
                                <Button className={style.btn_save} color='success' icon='user' onClick={() => {
                                    const image = getImage();
									const imageReplaced = image.replace('data:image/jpeg;base64,','');
									isUser(imageReplaced,inputRef.current.value);
                                }}>
                                    Assinar
                                </Button>
                            </div>
                            <div>
                                <Icon onClick={() => navigate(-1)} icon='reply' color= '#fff' size='5x' forceFamily= 'material' />
                            </div>
                        </div>
            </div>
    </div>
</main>
</div>

{/** FIM */}

				<Modal
					id='example-modal'
					isOpen={confirmationModal}
					titleId='example-title'>
						<ModalHeader className='modal-header-error'>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							
							<h1>Usuário não encontrado<br /></h1>
							Por favor, tente novamente.
						</ModalBody>
						<ModalFooter>
						
						<ButtonGroup isToolbar="true" size='lg'>
								<Button className='btn-style' color='success' icon='done' onClick={closeModal}>
									FECHAR
								</Button>								
						</ButtonGroup>
						</ModalFooter>
				</Modal>

				{
					/**
					 * CASO O USUÁRIO NÃO SEJA ENFERMEIRO, ABRIRÁ UMA MODAL INFORMANDO QUE O MESMO NÃO POSSUI AUTORIZAÇÃO.
					 */
				}
				<Modal
					id='modalAutorizacaoSusp'
					isOpen={modalPermissaoSuspensao}
					titleId='modalAutorizacaoSusp'>
						<ModalHeader style={{"backgroundColor":"#E5684C"}}>
							<ModalTitle id='example-title'>ATENÇÃO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h1>Você não possui autorização para suspender essa cirurgia!</h1>
							<br /><br />
						</ModalBody>
				</Modal>	
				<Modal
					id='sucess-modal'
				
					isOpen={modalSuccess}
					titleId='example-titlea'>
						<ModalHeader style={{"backgroundColor":"#37BBAA"}}>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
						<h1>ASSINADO COM SUCESSO!</h1>
						<br /><br />
						</ModalBody>
				</Modal>		



				<Modal
					id='loading'
					isOpen={Loading}
					titleId='loading'>
						<ModalHeader style={{"backgroundColor":"#636363"}}>
							<ModalTitle id='example-title'>ASSINANDO... 
							
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<br />
							<h1>Assinando...Por favor, aguarde...</h1>
							<br /><br />
						</ModalBody>
					
				</Modal>	

					
				<Modal isOpen={modalErrorMv}>
						<ModalHeader className='modal-header-error'>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h1>{msgErrorAss}</h1><br />
						</ModalBody>
						<ModalFooter>
						<ButtonGroup isToolbar="true" size='lg'>
								<Button className='btn-style' color='light' onClick={()=>setModalErrorMv(false)}>
									FECHAR
								</Button>								
						</ButtonGroup>
						</ModalFooter>
				</Modal>	

				

				<Modal
					isOpen={nofaceS3Error}>
						<ModalHeader className='modal-header-error'>
							<ModalTitle id='example-title'>AVISO</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							
							<h1>Face não cadastrada!<br /></h1>
							<h3>Por favor, realize o cadastro e tente novamente.</h3>
						</ModalBody>
						<ModalFooter>
						
						<ButtonGroup isToolbar="true" size='lg'>
								<Button className='btn-style' color='light' onClick={()=>setNofaceS3Error(false)}>
									FECHAR
								</Button>								
						</ButtonGroup>
						</ModalFooter>
				</Modal>
			</Page>
		</PageWrapper>
	);
};

export default AssinaturaCheckList;